:root {
  --light-background-color: #ffffff;
  --dark-background-color: #121212;
  --light-overlay-color: #00000080;
  --dark-overlay-color: #ffffff33;
  --light-select-color: #bcbcbc;
  --dark-select-color: #8c8c8c;
  --light-hover-color: #eeeeee;
  --dark-hover-color: #444444;
  --light-disabled-text-color: #c0c0c0;
  --dark-disabled-text-color: #555555;
  --annotation-text-light: #0000008f;
  --annotation-text-dark: #ffffff8f;
  --page-max-width-pc: 1200px;
  --page-max-width-tablet: 720px;
  --mygo-color: #3388bb;
  --mygo-tomori-color: #77bbdd;
  --mygo-anon-color: #ff8899;
  --mygo-rana-color: #77dd77;
  --mygo-soyo-color: #ffbb12;
  --mygo-taki-color: #7777aa;
  --hatsune-miku-color: #039393;
  --apple-site-light-gray: #f5f5f7;
}

html,
body {
  width: 100%;
  height: 100%;
}

p {
  margin: 0;
}

button {
  padding: 0;
}

* {
  caret-color: transparent;
}

/* Desktop Design */
@media (min-width: 1280px) {
  body {
    width: 100%;
    height: 100dvh;
  }
  * {
    font-size: 16px;
  }
  h1 {
    font-size: 24px;
  }
}
/* Tablet Design */
@media (min-width: 769px) and (max-width: 1279px) {
  body {
    width: 100%;
    height: 100dvh;
  }
  * {
    font-size: 16px;
  }
  h1 {
    font-size: 24px;
  }
}
/* Smartphone Design */
@media (max-width: 768px) {
  body {
    width: 100%;
    height: 100dvh;
  }
  * {
    font-size: 14px;
  }
  h1 {
    font-size: 20px;
  }
}
.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.absolute-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.invisible-scroll {
  overflow-y: scroll;
  scrollbar-width: none;
}
.invisible-scroll::-webkit-scrollbar {
  display: none;
}

.text-xs {
  font-size: 12px;
}

.text-sm {
  font-size: 14px;
}

.text-base {
  font-size: 16px;
}

.text-xl {
  font-size: 20px;
}

.text-2xl {
  font-size: 24px;
}

.text-base-bold {
  font-size: 16px;
  font-weight: bold;
}

.text-xl-bold {
  font-size: 20px;
  font-weight: bold;
}

.text-2xl-bold {
  font-size: 24px;
  color: red;
  font-weight: bold;
}

.font-bold {
  font-weight: bold;
}

.mb-8 {
  margin-bottom: 8px;
}

.mb-16 {
  margin-bottom: 16px;
}

.mb-24 {
  margin-bottom: 24px;
}

.button-light, .button-active-light {
  color: #000000;
  background-color: inherit;
  border: none;
}

.button-active-light:hover {
  background-color: var(--light-hover-color);
}
.button-active-light:disabled {
  color: var(--light-disabled-text-color);
  background-color: inherit;
}

.button-dark, .button-active-dark {
  color: #ffffff;
  background-color: inherit;
  border: none;
}

.button-active-dark:hover {
  background-color: var(--dark-hover-color);
}
.button-active-dark:disabled {
  color: var(--dark-disabled-text-color);
  background-color: inherit;
}

.link-light {
  color: #000000;
  padding: 4px 8px;
}
.link-light:hover {
  background-color: var(--light-hover-color);
}

.link-dark {
  color: #ffffff;
  padding: 4px 8px;
}
.link-dark:hover {
  background-color: var(--dark-hover-color);
}

.web-horizon-light {
  background-color: var(--light-disabled-text-color);
  border: none;
  height: 1px;
}

.web-horizon-dark {
  background-color: var(--dark-disabled-text-color);
  border: none;
  height: 1px;
}